.spollers {
	&__item {
		position: relative;
		border-top: 1px solid var(--color-grey-200);
		padding: 25px 0;

		@include mq(sm) {
			padding: 18px 0;
		}

		&:last-child {
			border-bottom: 1px solid var(--color-grey-200);
		}
	}

	&__button {
		@include flex-alignment(10px, space-between);

		width: 100%;
		font-size: 18px;
		line-height: 1.3;
		text-align: left;
		color: var(--color-dark-600);
		transition: color 0.3s;
		gap: 10px;

		@include mq(sm) {
			font-size: 14px;
		}

		svg {
			width: 24px;
			height: 24px;
			transform: rotate(45deg);
			transition:
				stroke 0.3s,
				transform 0.3s;

			@include mq(sm) {
				width: 20px;
				height: 20px;
			}
		}

		&--active {
			color: var(--color-orange-400);

			svg {
				transform: rotate(0);
			}
		}
	}

	&__body {
		padding-top: 10px;
		padding-right: 34px;

		@include mq(sm) {
			padding-top: 5px;
			padding-right: 24px;
			font-size: 12px;
		}
	}
}
